/*============================
#js_loading
============================*/
#js_loading {
  width: 100%;
  height: 100%;
  background: #5c3e86;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 20000; }

.spinner {
  width: 40px;
  height: 40px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -20px;
  margin-left: -20px; }

.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #fff;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out; }

.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s; }

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0); }
  50% {
    -webkit-transform: scale(1); } }

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0); }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1); } }

/*============================
#header
============================*/
#header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 90px;
  min-width: 1140px;
  background-color: #fff;
  z-index: 99;
  text-align: center;
  transition: .5s; }

#header.hide {
  transform: translateY(-100%); }

.global-nav {
  position: fixed;
  right: -320px;
  top: 0;
  width: 300px;
  padding-top: 20px;
  background-color: #fff;
  transition: all .6s;
  z-index: 200;
  overflow-x: hidden;
  overflow-y: auto;
  text-align: left; }

#header .leftLink {
  position: absolute;
  left: 20px;
  top: 36px; }

#header .leftLink ul li {
  float: left;
  margin-left: 14px;
  padding-left: 14px;
  display: inline-block;
  border-left: solid 1px #ccc; }

#header .leftLink ul li:first-child {
  margin-left: 0;
  padding-left: 0;
  border: none; }

#header .leftLink ul li a {
  font-size: 1em;
  font-weight: bold;
  line-height: 1.5; }

#header .logo {
  margin-top: 14px; }

#header .rightLink {
  position: absolute;
  right: 100px;
  top: 0; }

#header .rightLink .textSize {
  position: absolute;
  right: 130px;
  top: 0; }

#header .rightLink .textSize a {
  display: block;
  background: #777;
  color: #fff;
  padding: 6px 0 5px;
  width: 115px; }

#header .rightLink .language {
  position: absolute;
  right: 0;
  top: 0; }

#header .rightLink .language a {
  display: block;
  background: #bbb;
  color: #fff;
  padding: 6px 0 5px;
  width: 115px; }

#header .rightLink .member {
  position: absolute;
  right: 0;
  top: 25px; }

#header .rightLink .member a {
  background: #fff;
  padding: 12px 50px 11px; }

